body{
	width: 80%;
	margin: auto;
}

.header{
  font-size: 6vw;
  font-family: "Quicksand";
	text-align: center;
	align-content: center;
	margin-top: 5vw;
	margin-bottom: 5vw;
}

.gallery{
	display: flex;
}

.column{
  display: block;
	margin-right: .5vw;
	width: 100%;
}

.item{
	overflow: hidden;
	position: relative;
	margin-bottom: .5vw;
	box-shadow: 0.3rem 0.4rem 0.4rem rgba(0, 0, 0, 0.4);
	border-radius: 1%;
}

.imginfo{
	opacity: 0;
	bottom: -.78vw;
  left: .6vw;

  padding: .35vw;
	color: black;
  position: absolute;
  font-size: .65vw;
  font-family: "Quicksand";
  transition: .2s;
  background-color: white;
}



.imgdesc{
  font-size: .58vw;
  color: dimgray;
  display: block;
}

.img{
	display: block;
	position: relative;
	transition: .2s;
    height: 100%;
    width: 100%;
}